/* eslint-disable */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Navigation from '../../components/nav/Navigation';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
import { setUsernameActionCreator, setPasswordActionCreator, handleRegisterActionCreator } from '../../reducers/auth/authActionCreators';
import styles from './RegisterPageStyles';
/* eslint-enable */

const RegisterPage = ({
  classes, setUsername, setPassword, handleRegister
}) => (
  <div>
  <Navigation />
  <Paper className={classes.root}>
    <TextField
      color="inherit"
      type="email"
      label="Email"
      placeholder="Enter your Email"
      className={classes.textField}
      onChange={event => setUsername(event.target.value)}
    />
    <br/>
    <TextField
      color="inherit"
      type="password"
      label="Password"
      placeholder="Enter your Password"
      className={classes.textField}
      onChange={event => setPassword(event.target.value)}
    />
    <br/>
    <Button
      label="Submit"
      style={styles}
      variant="contained"
      color="primary"
      className={classes.button}
      onClick={event => handleRegister(event)}
    >
      Register
    </Button>
  </Paper>
  </div>
);

RegisterPage.propTypes = {
  classes: PropTypes.object.isRequired,
  setUsername: PropTypes.func.isRequired,
  setPassword: PropTypes.func.isRequired,
  handleRegister: PropTypes.func.isRequired,
};

const StyledRegisterPage = withStyles(styles)(RegisterPage);

const mapStateToProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToProps = (dispatch, ownProps) => ({
  setUsername: username => dispatch(setUsernameActionCreator(username)),
  setPassword: password => dispatch(setPasswordActionCreator(password)),
  handleRegister: () => dispatch(handleRegisterActionCreator(ownProps.authInfo))
});
const ReduxRegisterPage = connect(
  mapStateToProps,
  mapDispatchToProps
)(StyledRegisterPage);

export default ReduxRegisterPage;
